import { useParams, Link } from 'react-router-dom';
import { useState, useEffect } from 'react';

import useMarvelService from '../../services/MarvelService'
import Spinner from '../spinner/Spinner';
import ErrorMessage from '../errorMessage/ErrorMessage';

import './singleComicPage.scss';

const SingleComicPage = () => {
  /* Достаем переменную comicId из обьекта с параметрами */
  const { comicId } = useParams();
  const [comic, setComic] = useState({desctiption: "hello"});

  const {loading, error, getComic, clearError} = useMarvelService();

  useEffect(() => {
    updateComic();

    /* Обновляем комикс при изменении айди */
    //eslint-disable-next-line
  }, [comicId])

  const updateComic = () => {
      clearError();      
      getComic(comicId)
          .then(onComicLoaded)
      
  }


  /* Когда загрузился, записываем персон в стейт */
  const onComicLoaded = (comic) => {
    setComic(comic);
  } 

  /* Условные отрисовки */
    const errorMessage = error ? <ErrorMessage/> : null;
    const spinner = loading ? <Spinner/> : null;
    const content = !(loading || error || !comic) ? <View comic={comic}/> : null
  
  return (
    <>
      {errorMessage}
      {spinner}
      {content}
    </>
  )
  
}


const View = ({comic}) => {

  const {title, description, pageCount, thumbnail, language, price} = comic;

  return (
    <div className="single-comic">
        <img src={thumbnail} alt={title} className="single-comic__img"/>
        <div className="single-comic__info">
            <h2 className="single-comic__name">{title}</h2>
            <p className="single-comic__descr">{description}</p>
            <p className="single-comic__descr">Pages: {pageCount}</p>
            <p className="single-comic__descr">Language: {language}</p>
            <div className="single-comic__price">{price}</div>
        </div>
        <Link to="/comics" className="single-comic__back">Back to all</Link>
    </div>
)
}

export default SingleComicPage;